import "../styles/css/App.css";
import { BrowserRouter as Router, Route } from "react-router-dom";
import Home from "../components/pages/Home";
import { Fragment } from "react";
function App() {
    return (
        <Router>
            <Fragment>
                <Route path="/" component={Home} exact></Route>
            </Fragment>
        </Router>
    );
}

export default App;
